<template>
  <div class="recommend-page" ref="recommendPage"  >
    <scroll ref="recommendContent" class="recommend-content" :data="discList">
      <div >
        <!--轮播-->
        <div v-if="carousel.length" class="carousel-wrapper">
          <slider ref="slider">
            <div v-for="item in carousel" class="carousel-item">
              <a :href="item.linkUrl">
                <img :src="item.picUrl" @load="loadImage">
              </a>
            </div>
          </slider>
        </div>
        <!--推荐列表-->
        <div class="disc-list" >
          <h1 class="title">热门歌单推荐</h1>
          <ul>
            <li v-for="disc in discList" class="item" @click="selectItem(disc)">
              <div class="icon">
                <img v-lazy="disc.imgurl" width="60" height="60"/>
              </div>
              <div class="text">
                <h2 class="name" v-html="disc.creator.name"></h2>
                <p class="desc" v-html="disc.dissname"></p>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <!--正在加载-->
      <div class="loading-wrapper" v-show="!discList.length">
        <loading></loading>
      </div>
    </scroll>
    <router-view></router-view>
  </div>
</template>

<script>
  import Loading from '../../base/loading/loading.vue'
  import Scroll from '../../base/scroll/scroll.vue'
  import Slider from '../../base/slider/slider.vue'
  import {getCarousel, getDiscList} from '../../api/recommend.js'
  import {playListMixin} from '../../common/js/mixin'
  import {ERR_OK} from '../../api/config'
  import {mapMutations} from'vuex'

  export default {
    mixins: [playListMixin],
    data() {
      return {
        carousel: {},
        discList: []
      }
    },
    created() {
      this.receiveCarousel();
      this.receiveDiscList();
    },
//    activated() {
//      setTimeout(() => {
//        this.$refs.slider && this.$refs.slider.refresh()
//      }, 20)
//    },
    methods: {
      //获得轮播图
      receiveCarousel() {
        getCarousel().then((res) => {
          if (res.code === ERR_OK) {
            this.carousel = res.data.slider;
          }
        })
      },
      //获得推荐列表
      receiveDiscList() {
        getDiscList().then((res) => {
          if (res.code === ERR_OK) {
            this.discList = res.data.list;
          }
        })
      },
      //轮播图片加载
      loadImage() {
        if (!this.checkloaded) {
          this.checkloaded = true;
          this.$refs.recommendContent.refresh()
          //scroll组件的高度不仅包含推荐列表，也包含轮播图。
          // 但是观测data数据只有推荐列表。
          // 如果轮播高度改变，data不变，scroll不刷新。
          // 但是我们希望它刷新。
        }
      },
      //选择一个推荐唱片
      selectItem(disc){
        this.$router.push({//相当于<link to= ...>
          path:`/recommend-page/${disc.dissid}`
        });
        this.setDisc(disc)
      },
      //底部保留（mini播放器）
      handlePlayList(playList) {
        const bottom = playList.length > 0 ? '60px' : '';
        this.$refs.recommendPage.style.bottom = bottom;
        this.$refs.recommendContent.refresh()
      },
      ...mapMutations({
        //setTopList这个函数映射到了SET_TOP_LIST这个mutations-type的值'SET_TOP_LIST'，
        // vuex可以拿到函数setTopList传给他的值，数据共享
        setDisc:'SET_DISC'
      })
    },
    components: {
      Scroll,
      Slider,
      Loading
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .recommend-page
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    .recommend-content
      height: 100%
      overflow: hidden
      .carousel-wrapper
        position: relative
        width: 100%
        overflow: hidden
      .disc-list
        .title
          height: 65px
          line-height: 65px
          text-align: center
          font-size: $font-size-medium
          color: $color-theme
        .item
          display: flex
          box-sizing: border-box
          align-items: center
          padding: 0 20px 20px 20px
          .icon
            flex: 0 0 60px
            width: 60px
            padding-right: 20px
          .text
            display: flex
            flex-direction: column
            justify-content: center
            flex: 1
            line-height: 20px
            overflow: hidden
            font-size: $font-size-medium
            .name
              margin-bottom: 10px
              color: $color-text
            .desc
              color: $color-text-d
      .loading-wrapper
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>



