<template>
  <div class="carousel" ref="slider">
    <div class="carousel-group" ref="sliderGroup">
      <slot></slot>
    </div>
    <div class="dots" ref="dots">
      <span class="dot" v-for="(item,index) in dots" :class="{active:currentPageIndex-1===index}"></span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'

  export default {
    name: 'slider',
    props: {
      loop: {
        type: Boolean,
        default: true
      },
      autoPlay: {
        type: Boolean,
        default: true
      },
      interval: {
        type: Number,
        default: 4000
      }
    },
    data() {
      return {
        //小点 组
        dots: [],
        //所在轮播页
        currentPageIndex: 1,
      }
    },
    created() {
    },
    mounted() {
      setTimeout(() => {//异步是因为获取图片需要一定时间
        this._setSliderWidth();
        this._initDots();
        this.initSlider();
      }, 20);

      //窗口改变时
      window.addEventListener('resize', () => {
        if (!this.slider || !this.slider.enabled) {
          return
        }
//        clearTimeout(this.resizeTimer);
//        this.resizeTimer = setTimeout(() => {
//          if (this.slider.isInTransition) {
//            this.onScrollEnd()
//          }
//          else {
//            if (this.autoPlay) {
//              this._play()
//            }
//          }
//          this.refresh()
//        }, 60);
        //重置孩子，父亲，的宽度
        this._setSliderWidth(true);
        this.slider.refresh()
      });

    },
    beforeDestroy() {
      clearTimeout(this.timer)
    },
    methods: {
      //初始化宽度
      _setSliderWidth(isResize) {
        this.group = this.$refs.sliderGroup.children;
        let groupWidth = 0;//轮播滑条（爸爸）宽度初始值
        let sliderWidth = this.$refs.slider.clientWidth;//轮播视口（爷爷）宽度

        for (let i = 0; i < this.group.length; i++) {
          let child = this.group[i];
          child.style.width = sliderWidth + 'px';//孩子宽度=爷爷宽度
          groupWidth += sliderWidth//爸爸宽度初始值=爷爷宽度叠加
        }
        //循环无缝轮播 爸爸宽度=爷爷宽度叠加+2
        if (this.loop && !isResize) {
          //如果循环无缝轮播时，重置窗口，重新设置宽度则不需要叠加+2
          groupWidth += 2 * sliderWidth
        }
        this.$refs.sliderGroup.style.width = groupWidth + 'px'//爸爸宽度最终值
      },
      //初始化圆点
      _initDots() {
        this.dots = new Array(this.group.length)
      },
      //轮播初始化
      initSlider() {
        //轮播为BScroll实例
        this.slider = new BScroll(this.$refs.slider, {
          scrollX: true,//允许横向滑动 不支持纵向滑动
          scrollY: false,
          momentum: false,//不启用惯性
          snap: {
            loop: this.loop,//无缝循环轮播，设置切换动作和停留总时间
            threshold: 0.3,//用手指滑动时页面，大于0.3（阈值）可以滑动到下一页
            speed: 400//切换动作时间
          }});

          if(this.autoPlay){
            this.play();//第一次切换
            this.slider.on('scrollEnd', this.play);//监听，连续切换
          }
      },

      //轮播切换
      play() {
        this.currentPageIndex=this.slider.getCurrentPage().pageX+1;//设置圆点
        clearTimeout(this.timer);
        this.timer = setTimeout(() => {
          this.slider.next();
        }, this.interval);
      },
    },
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .carousel
    min-height: 1px
    .carousel-group
      position: relative
      overflow: hidden
      white-space: nowrap
      .carousel-item
        float: left
        box-sizing: border-box
        overflow: hidden
        text-align: center
        a
          display: block
          width: 100%
          overflow: hidden
          text-decoration: none
        img
          display: block
          width: 100%
    .dots
      position: absolute
      right: 0
      left: 0
      bottom: 12px
      text-align: center
      font-size: 0
      .dot
        display: inline-block
        margin: 0 4px
        width: 8px
        height: 8px
        border-radius: 50%
        background: $color-text-l
        &.active
          width: 20px
          border-radius: 5px
          background: $color-text-ll
</style>



