import {jsonp} from '../common/js/jsonp.js'
import {commonParams, commonOption,debug} from './config.js'
import axios from 'axios'


export function getCarousel() {
  const url = 'https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg';
  const data = Object.assign({}, commonParams, {
    platform: 'h5',
    uin: 0,
    needNewCode: 1
  });
  return jsonp(url, data, commonOption);
}

export function getDiscList() {
  const url = debug ? '/api/getDiscList' : 'http://www.fairyhan.cn/api/getDiscList';
  const data = Object.assign({}, commonParams, {
    picmid: 1,
    rnd: Math.random(),
    loginUin: 0,
    hostUin: 0,
    platform: 'yqq',
    needNewCode: 0,
    categoryId: 10000000,
    sortId: 5,
    sin: 0,
    ein: 29,
    format: 'json'
  });
  const option = Object.assign({}, commonOption, {
    name: 'getPlaylist'
  });
  return axios.get(url, {params: data})
    .then((resFromSever) => {
      return Promise.resolve(resFromSever.data)
    })
}

export function getDiscDetail(disstid) {
  const url=debug ?'/api/getDiscDetail':'http://www.fairyhan.cn/api/getDiscDetail';
  const data=Object.assign({},commonParams,{
    disstid:disstid,
    type: 1,
    json: 1,
    utf8: 1,
    onlysong: 0,
    platform: 'yqq',
    hostUin: 0,
    needNewCode: 0,
    loginUin:0,
    format: 'json'
  });
  const option = Object.assign({}, commonOption, {
    name: 'playlistinfoCallback'
  });
  return axios.get(url, {params: data})
    .then((resFromSever) => {
      return Promise.resolve(resFromSever.data)
    })
}



